@import url(https://fonts.googleapis.com/css2?family=Cormorant+Unicase:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{display:flex;justify-content:center;flex-direction:column;align-items:center;position:absolute;text-align:center;padding:10px;top:0;right:0;bottom:0;left:0;background-image:url(/public/static/media/logo.2cf504f9.svg);background-repeat:no-repeat;background-position:center;background-color:#273864;color:#f0ffff;font-family:'Cormorant Unicase', serif}.title{position:absolute;top:0;font-size:2rem;text-transform:uppercase}.selection-container{display:flex;flex-direction:column}

